import React, { useState } from 'react'
import { Button, Container, Form, InputGroup, Row, Table } from 'react-bootstrap'
import axios from 'axios'
import { message } from 'antd'

const FindStaff = () => {
  const [searchedDoctor, setSearchedDoctor] = useState(false)
  const [searchedReceptionist, setSearchedReceptionist] = useState(false)
  const [userId, setUserId] = useState('')
  const [doctorData, setDoctorData] = useState({})
  const [receptionistData, setReceptionistData] = useState({})
  const handleSearch = async (e) => {
    try {
      e.preventDefault()
      e.stopPropagation()

      const res = await axios.post('/api/v1/admin/find-staff', { userId })
      if (res.data.success) {
        if (res.data.data.uniqueId.includes('DOC')) {
          console.log(res.data.data.name)
          setSearchedDoctor(true)
          setDoctorData(res.data.data)
        }
        if (res.data.data.uniqueId.includes('REC')) {
          setSearchedReceptionist(true)
          console.log(res.data.data.name)
          setReceptionistData(res.data.data)
        }
        message.success(res.data.message)
      }
    } catch (error) {
      console.log(error)
      message.error(error.response.data.message)
    }
  }
  return (
    <Container>
      <div className="d-flex justify-content-end">
        <Form onSubmit={handleSearch}>
          <InputGroup className="mb-3">
            <Form.Group controlId="Search_ID">
              <Form.Control
                type="text"
                placeholder="Enter Staff ID"
                value={userId}
                onChange={(e) => setUserId(e.target.value)}
              />
            </Form.Group>
            <Button type="submit" className="bg-info border-0">
              Search
            </Button>
          </InputGroup>
        </Form>
      </div>
      <Row>
        {searchedDoctor && (
          <div className="d-flex justify-content-center">
            <div className="card" style={{ width: '18rem' }}>
              <div className="card-body">
                <h5 className="card-title">{doctorData.name}</h5>
                <h6 className="card-subtitle mb-2 text-muted">{doctorData.uniqueId}</h6>
                <p className="card-text">{doctorData.email}</p>
                <p className="card-text">Role : Doctor</p>
                <p className="card-text">{doctorData.mobileNumber}</p>
                <p className="card-text">{doctorData.address}</p>
                <p className="card-text">{doctorData.zip}</p>
                <p className="card-text">{doctorData.experience}</p>
                <p className="card-text">{doctorData.specialization}</p>
              </div>
            </div>
          </div>
        )}
        {searchedReceptionist && (
          <div className="d-flex justify-content-center">
            <div className="card" style={{ width: '18rem' }}>
              <div className="card-body">
                <h5 className="card-title">{receptionistData.name}</h5>
                <h6 className="card-subtitle mb-2 text-muted">{receptionistData.uniqueId}</h6>
                <p className="card-text">{receptionistData.email}</p>
                <p className="card-text">Role : Receptionist</p>
                <p className="card-text">{receptionistData.mobileNumber}</p>
                <p className="card-text">{receptionistData.address}</p>
                <p className="card-text">{receptionistData.zip}</p>
              </div>
            </div>
          </div>
        )}
      </Row>
      {/* <Row className='  '>
        {
          searchedReceptionist && <div className=''>
            <Table responsive className='p-5 w-50'>
              <tbody className='text-center'>
                <tr className=''>
                  <th>Name</th>
                  <td>{receptionistData.name}</td>
                </tr>
                <tr>
                  <th>Unique ID</th>
                  <td>{receptionistData.uniqueId}</td>
                </tr>
                <tr>
                  <th>Email</th>
                  <td>{receptionistData.email}</td>
                </tr>
                <tr>
                  <th>Role</th>
                  <td>Receptionist</td>
                </tr>
                <tr>
                  <th>Mobile Number</th>
                  <td>{receptionistData.mobileNumber}</td>
                </tr>
                <tr>
                  <th>Address</th>
                  <td>{receptionistData.address}</td>
                </tr>
                <tr>
                  <th>Zip Code</th>
                  <td>{receptionistData.zip}</td>
                </tr>
              </tbody>
            </Table>
          </div>
        }
      </Row> */}
    </Container>
  )
}

export default FindStaff
